import React ,{useState}from "react";
import "./Style.css";

const UseState = () => {
  const initialData = 10;
  const [myNum, setMyNum] = useState(initialData);
  console.log(myNum);

  return (
    <>
      <div className="center_div">
        <p> {myNum}</p>
        <div class="button2" onClick={()=>{setMyNum(5)}}>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
         INCR
        </div>
        <div
          class="button2">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          DECR
        </div>
      </div>
    </>
  );
};

export default UseState;